import { IPost } from '../types/post';

export const setMetadatas = (post?: IPost) => {
    const { tags = 'salgum1114,blog,react,antd,webpack,css,javascript', path = '', cover = '/static/images/authors/salgum1114.png', title = 'Dev.log', preview = 'salgum1114 Dev.log' } = post;
    const description = document.querySelector('meta[name="description"]');
    description.setAttribute('content', preview);
    const keywords = document.querySelector('meta[name="keywords"]')
    keywords.setAttribute('content', tags);
    const ogTitle = document.querySelector('meta[property="og:title"]');
    ogTitle.setAttribute('content', title);
    const ogDescription = document.querySelector('meta[property="og:description"]');
    ogDescription.setAttribute('content', preview);
    const ogUrl = document.querySelector('meta[property="og:url"]');
    ogUrl.setAttribute('content', `https://salgum1114.github.io${path}`);
    const ogImage = document.querySelector('meta[property="og:image"]');
    ogImage.setAttribute('content', `https://salgum1114.github.io${cover}`);
    const twitterTitle = document.querySelector('meta[name="twitter:title"]');
    twitterTitle.setAttribute('content', title);
    const twitterImage = document.querySelector('meta[name="twitter:image"]');
    twitterImage.setAttribute('content', `https://salgum1114.github.io${cover}`);
}